	module.exports = {"data":{"post":{"html":"<p>It's the day after, and <a href=\"http://www.spiegel.de/\">Spiegel Online</a> is full of resignations. Möllemann resigns from his national FDP party offices (but not as chairman in NRW). Däubler-Gmelin announces she won't be available for the new cabinet (as if Schröder was going to ask). And Merz is making way for Merkel to be Bundestag minority leader. The teams might be the same, but you're going to have to buy a new program to tell the players.</p>","fields":{"title":"Day of Resignation","date":"23 Sep 2002","url":"https://papascott.de/2002-09-23-day-of-resignation/"}}},"pathContext":{"slug":"/2002-09-23-day-of-resignation/","prev":{"fields":{"title":"Pre-Game Show","date":"22 Sep 2002","slug":"/2002-09-22-pre-game-show/"}},"next":{"fields":{"title":"Schill Party: 0.6%","date":"23 Sep 2002","slug":"/2002-09-23-schill-party-06/"}}}}

